import { Client, types as CassandraTypes } from 'cassandra-driver';

// Provide rudimentary connection to DSE cluster
const client = new Client(
  { 
    contactPoints: [process.env.CONTACT_POINTS], 
    localDataCenter: process.env.DATACENTER_NAME/**,
    queryOptions: { consistency: types.consistencies.quorum }*/
  }
);

client.connect()
  .then(function () {
    console.log('Connected to cluster with %d host(s): %j', client.hosts.length, client.hosts.keys());
    console.log('Keyspaces: %j', Object.keys(client.metadata.keyspaces));
  })
  .catch(function (err) {
    console.error('There was an error when connecting', err);
    return client.shutdown().then(() => { throw err; });
  });


module.exports.performQuery = function (call, callback) { 
  return new Promise(function(resolve, reject) {
    return client.execute("select * from killrvideo.videos limit 1")
      .then(result => {
        let video = result.first();
        resolve(video);
      });
  });
};

